:root{
  --blue: #0E389C;
  --green: #079A5A;
}

.container{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 70px;
}

.container2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 36px;
  gap: 36px;
}

.title {
  background-color: #0E389C;
  background-color: var(--blue);
  color: white;
  font-size: 20px;
  font-weight: 700;
  padding: 2px 2.6vw;
}

.bttns{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #0E389C;
  background-color: var(--blue);
  width: 70vw;
  height: 6vh;
}

.btn {
  border: none;
  background-color: #0E389C;
  background-color: var(--blue);
  color: white;
  font-family: 'Poppins';
  font-weight: 400;
  font-size: 17px;
  height: 100%;
  width: 100%;
}

.btn:hover{
  background-color: #103488;
}

.selected {
  font-weight: 700;
}

.playerContainer {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 70vw;
  height: 17vh;
  grid-gap: 38px;
  gap: 38px;
}

.box {
  background-color: #D9D9D9;
  border-top: 4px solid #0E389C;
  border-top: 4px solid var(--blue);
  height: 11vw;
  max-height: 80%;
  width: 11vw;
  max-width: 95px;
  margin-left: 4vw;
}

.evenBox {
  border-top: 4px solid #079A5A;
  border-top: 4px solid var(--green);
}

.box + div{
  display:flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  height: 80%;
}

.even {
  background-color: #EEEEEE;
}

.name, .age{
  font-size: 18px;
  margin: 0;
  padding: 0;
}

.name {
  color: #0E389C;
  color: var(--blue);
  font-weight: 700;
}

.age {
  color: #0E389C;
  color: var(--blue);
  font-weight: 400;
}

.evenAge, .evenName {
  color: #079A5A;
  color: var(--green);
}

.playerIcon{
  border-radius: 100%;
  height: 64px;
  width: 64px;
  background-color: #0E389C;
  background-color: var(--blue);
  color: white;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
  /* Line height should be same as height */
  line-height: 64px;
  margin-left: auto;
  margin-right: 4vw;
}

.evenIcon {
  background-color: #079A5A;
  background-color: var(--green);
}

@media only screen and (max-width: 600px) {
  .bttns{
    flex-direction: column;
    height: auto;
  }
  
  .btn{
    height: 36px;
  }

  .playerContainer{
    grid-gap: 5%;
    gap: 5%;
  }

  .box{
    visibility: hidden;
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
  }

  .box + div{
    width: 60%;
    height: 100%;
  }

  .name, .age {
    font-size: 5vw;
  }

  .playerIcon{
    min-height: 60px;
    min-width: 60px;
    line-height: 60px;
  }
}
